import React from "react";
import Layout from '../layout/Layout';
import { Typography, Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Metadata from '../components/Metadata';
import {LoginPageMetadata} from '../configs/PageMetadata';
import { AuthenticatedTemplate, UnauthenticatedTemplate } from "@azure/msal-react";
import { useMsal, useAccount } from "@azure/msal-react";
import { InteractionStatus} from "@azure/msal-browser";
import { loginRequest } from "../configs/AuthConfig";
import { callMsGraph } from "../services/GraphService";
import { scopeGraphAPI } from '../configs/AuthConfig';

const useStyles = makeStyles(() => ({
    root: {
        margin: 20,
        padding: 20
    },
    sectionTitle: {
        paddingBottom: 10
    }
}));


export default function LoginPage() {
    const classes = useStyles();
    const { instance, accounts, inProgress } = useMsal();
    const account = useAccount(accounts[0] || {});

    if (account && inProgress === InteractionStatus.None) {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: account,
            scopes: scopeGraphAPI
        }).then((response) => {
            callMsGraph(response.accessToken).then(response => console.log(response));
        });
    }

    return (
        <Layout>
            <Paper elevation={3} className={classes.root} >
                <div className={classes.sectionTitle}>
                    <AuthenticatedTemplate>
                    <Typography variant="h6">
                        <center>Logged-In.</center>
                        </Typography>
                    </AuthenticatedTemplate>

                    <UnauthenticatedTemplate>
                        <Typography variant="h6">
                        <center>Please sign-in to see your profile information.</center>
                        </Typography>
                    </UnauthenticatedTemplate>
                </div>              
            </Paper>
            <Metadata
        Title={LoginPageMetadata.Title}
        Description={LoginPageMetadata.Description}
      />
        </Layout>
    );
}
